
.main-wrapper {
  background: #fff;
  width: 92%;
  margin: 0 auto;
  border-radius: 4px;
  overflow: hidden;
  padding: 24px 0 0;
  margin-bottom: 24px;
  position: relative;
}
.main-container {
  padding: 0 6% 120px 4% !important;
  margin-left: -1px;
  min-height: 500px;
  border-left: 1px solid #e9e9e9;
}
.page-container {
  /*color: #666;*/
  font-size: 14px;
  line-height: 1.5;
}
.page-container h1 {
  color: #404040;
  font-weight: 500;
  line-height: 40px;
  margin-bottom: 24px;
  margin-top: 8px;
  font-size: 28px;
  font-family: lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
}
.page-container > h2 {
  font-size: 24px;
}
.page-container hr {
  border-radius: 10px;
  height: 3px;
  border: 0;
  background: #eee;
  margin: 20px 0;
}
.page-container code,
.page-container kbd,
.page-container pre,
.page-container samp {
  font-family: Consolas, monospace;
}
.page-container code {
  margin: 0 3px;
}
.page-container > ul li {
  list-style: circle;
  margin-left: 20px;
}
.page-container > ol li {
  list-style: decimal;
  margin-left: 20px;
  padding-left: 8px;
}
.page-container .table {
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0px;
  empty-cells: show;
  border: 1px solid #e9e9e9;
  width: 100%;
  margin: 16px 0;
}
.page-container .table th {
  background: #F7F7F7;
  white-space: nowrap;
  color: #5C6B77;
  font-weight: 600;
}
.page-container .table td:first-child {
  background: #fcfcfc;
  font-weight: 500;
  width: 20%;
  font-family: "Lucida Console", Consolas, Menlo, Courier, monospace;
}
.page-container .table th,
.page-container .table td {
  border: 1px solid #e9e9e9;
  padding: 8px 16px;
  text-align: left;
}
.main-container > section > h3 {
  color: #404040;
  font-family: lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
  margin: 1.6em 0 .6em;
  font-weight: 500;
  clear: both;
}
.code-boxes-col-2-1 {
  display: inline-block;
  vertical-align: top;
  padding: 0 8px;
}
.aside-container {
  padding-bottom: 50px;
}
.aside-container .ant-menu-item a,
.aside-container .ant-menu-submenu-name span,
.aside-container > .ant-menu-item {
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.header-anchor {
  margin-left: -18px;
}
.markdown > ul {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.markdown > ul > li {
  list-style: circle;
  margin-left: 1rem;
}
.markdown h1 a,
.markdown h2 a,
.markdown h3 a {
  font-size: .8em;
  opacity: 0;
  font-weight: normal;
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}
.markdown h1:hover a,
.markdown h2:hover a,
.markdown h3:hover a {
  opacity: 1;
}
.markdown blockquote {
  font-size: 90%;
  color: #999;
  border-left: 4px solid #e9e9e9;
  padding-left: 0.8em;
  margin: 1em 0;
}
.markdown code {
  margin: 0 1px;
  background: #f7f7f7;
  padding: .2em .4em;
  border-radius: 3px;
  font-size: .9em;
  border: 1px solid #eee;
}
.markdown > p,
.markdown > pre {
  margin-top: 1rem;
  margin-bottom: 1rem;
}



